.drag-n-drop-wrapper
{
    position:relative;
    width: 100%;
    height: 25vh;
    background-color: lightgrey;
}
.drag-n-drop
{
    position:absolute;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    filter: drop-shadow( 3px 3px 2px rgba(0, 0, 0, .7));
}
.drag-n-drop-wrapper .svg-wrapper
{
    pointer-events: none;
    position:absolute;
    top:0;
    left: 0;
    width:100%;
    height:100%;
}
.drag-n-drop-wrapper .svg-wrapper svg 
{
    pointer-events: none;
    position: absolute;
    top:0;
    left: 0;
    width:100%;
    height: 100%;
}
.drag-n-drop-wrapper .svg-wrapper svg line
{
    pointer-events: auto;
    z-index: 5;
    stroke-width: 4px;
    filter: drop-shadow( 3px 3px 2px rgba(0, 0, 0, .2));
}
svg line.static
{
    stroke: green;
    pointer-events: all;
}
svg line.static:hover
{
    cursor:not-allowed;
}